.nav-bar-button {
  display: inline-block;
  font-size: 1.8vh;
  width: 19%;
  height: 100%;
  color: white;
  background-color: hsl(30, 5%, 15%);
  /* border: 2px solid hsl(45, 75%, 50%); */
  border: none;
  border-radius: 0px;
  transition-duration: 0.25s;
  padding: 0px;
  margin: 0px;
  cursor: pointer;

  text-align: center;
  
  font-family: Montserrat-Light;
  user-select: none;
}
.nav-bar-button:hover {
  background-color: hsl(30, 5%, 17%);
  color: hsl(45, 75%, 50%);

}
.nav-bar-button:active {
  background-color: hsl(45, 75%, 50%);
  border-color: hsl(30, 5%, 25%);
  color: white;
  transform: scale(90%);
}
.button-link {
  display: block;
  width: 100%;
  height: 100%;
  color: white;
  transition-duration: 0.25s;
}
.button-link:hover {
  background-color: hsl(30, 5%, 17%);
  color: hsl(45, 75%, 50%);
}
.button-link:active {
  background-color: hsl(45, 75%, 50%);
  border-color: hsl(30, 5%, 25%);
  color: white;
  transform: scale(90%);
}
.nav-bar-button-text {
  display: inline-block;
  line-height: 8vh;
}

.border-left {
  float: left;
  position: relative;
  background-color: hsl(45, 75%, 50%);
  width: 1px;
  height: 80%;
  top: 10%;
}

.border-right {
  float: right;
  position: relative;
  background-color: hsl(45, 75%, 50%);
  width: 1px;
  height: 80%;
  top: 10%;
}
